/* 设计稿, 以iphone6为准 */
/* 颜色规范 */
/* 字体规范 */
.wrapper {
  position: relative;
  width: 100%; }

.decorate {
  position: absolute;
  top: 0;
  z-index: 100;
  width: 100%; }
  .decorate .logo {
    position: relative;
    top: -0.14667rem;
    margin: 0 auto;
    padding: 0;
    text-align: center;
    font-size: 0.25333rem;
    font-weight: 400; }
  .decorate .left, .decorate .right {
    position: absolute;
    top: 0.29333rem;
    width: 0.32rem; }
  .decorate .left {
    left: 0.18667rem; }
  .decorate .right {
    right: 0.18667rem; }
  .decorate .dotWrapper {
    position: relative;
    top: -0.29333rem;
    z-index: 100;
    margin: 0 auto;
    width: 3.76rem; }
    .decorate .dotWrapper .dotsLeft, .decorate .dotWrapper .dotsRight {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      display: flex; }
      .decorate .dotWrapper .dotsLeft .dot, .decorate .dotWrapper .dotsRight .dot {
        display: inline-block;
        width: 0.08rem;
        height: 0.08rem;
        background-color: #111; }
    .decorate .dotWrapper .dotsLeft .dot {
      margin-right: 0.08rem; }
      .decorate .dotWrapper .dotsLeft .dot:first-child {
        width: 0.32rem; }
    .decorate .dotWrapper .dotsRight {
      justify-content: flex-end;
      /* stylelint-disable-next-line */ }
      .decorate .dotWrapper .dotsRight .dot {
        margin-left: 0.08rem; }
        .decorate .dotWrapper .dotsRight .dot:last-child {
          width: 0.32rem; }

.center {
  position: relative;
  z-index: 99;
  margin: 0 auto;
  padding: 0.32rem;
  width: 3.76rem;
  border: 0.08rem solid #111;
  border-top: none;
  border-radius: 0 0 0.06667rem 0.06667rem; }
  .center .screenWrapper {
    width: 100%;
    border: 0.03333rem solid;
    border-color: #987f0f #fae36c #fae36c #987f0f;
    background-color: #9ead86; }
    .center .screenWrapper .screen {
      padding: 0.06667rem;
      width: 100%;
      border: 0.01333rem solid #453e36;
      display: flex; }
      .center .screenWrapper .screen .mainPanel {
        padding: 0.01333rem 0.02667rem;
        width: 1.73333rem;
        border: 0.02667rem solid #000000;
        font-size: 0; }

.statusPanel {
  position: relative;
  width: 1.06667rem; }
  .statusPanel .sound {
    position: absolute;
    bottom: 0.06667rem;
    left: 0.06667rem; }
  .statusPanel .pause {
    position: absolute;
    bottom: 0.06667rem;
    left: 0.29333rem; }
  .statusPanel .time {
    position: absolute;
    bottom: 0.06667rem;
    right: 0; }
