/* 设计稿, 以iphone6为准 */
/* 颜色规范 */
/* 字体规范 */
.character {
  position: relative;
  display: inline-block;
  width: 0.08667rem;
  height: 0.16rem;
  margin-left: 1px;
  background-image: url(../../assets/images/icon.png);
  background-repeat: no-repeat;
  background-size: 2.66667rem 1.24rem;
  background-position: -1.43333rem -0.16667rem;
  overflow: hidden; }

.character_0 {
  background-position: -0.5rem -0.16667rem; }

.character_1 {
  background-position: -0.59333rem -0.16667rem; }

.character_2 {
  background-position: -0.68667rem -0.16667rem; }

.character_3 {
  background-position: -0.78rem -0.16667rem; }

.character_4 {
  background-position: -0.87333rem -0.16667rem; }

.character_5 {
  background-position: -0.96667rem -0.16667rem; }

.character_6 {
  background-position: -1.06rem -0.16667rem; }

.character_7 {
  background-position: -1.15333rem -0.16667rem; }

.character_8 {
  background-position: -1.24667rem -0.16667rem; }

.character_9 {
  background-position: -1.34rem -0.16667rem; }

.character_ {
  background-position: -1.43333rem -0.16667rem; }

.character_colon {
  width: 0.10667rem;
  background-position: -1.52667rem -0.16667rem;
  animation: colonflash 2000ms infinite; }

.character_sound_off {
  width: 0.16667rem;
  height: 0.14rem;
  background-position: -1rem -0.5rem; }

.character_sound_on {
  width: 0.16667rem;
  height: 0.14rem;
  background-position: -1.16667rem -0.5rem; }

.character_pause_on {
  width: 0.16667rem;
  height: 0.14rem;
  background-position: -0.5rem -0.5rem;
  animation: pauseflash 500ms infinite; }

.character_pause_off {
  width: 0.16667rem;
  height: 0.14rem;
  background-position: -0.66667rem -0.5rem; }

@keyframes pauseflash {
  0% {
    background-position: -0.5rem -0.5rem; }
  50% {
    background-position: -0.5rem -0.5rem; }
  51% {
    background-position: -0.66667rem -0.5rem; }
  100% {
    background-position: -0.66667rem -0.5rem; } }

@keyframes colonflash {
  0% {
    background-position: -1.52667rem -0.16667rem; }
  50% {
    background-position: -1.52667rem -0.16667rem; }
  51% {
    background-position: -1.62rem -0.16667rem; }
  100% {
    background-position: -1.62rem -0.16667rem; } }
