.app {
  position: relative;
  margin: 0 auto;
  padding-top: 0.77333rem;
  min-height: 100vh;
  font-size: 0.21333rem;
  background-color: #efcc19;
  animation: fadeIn 0.5s; }
  @media screen and (min-width: 540px) {
    .app {
      border-radius: 20px;
      padding-top: 0.42667rem; } }

.loading {
  margin: 0.77333rem auto;
  width: 1.33333rem !important;
  height: 1.33333rem !important;
  fill: #efcc19 !important;
  animation: fadeOut 0.5s; }

@keyframes fadeIn {
  0% {
    opacity: 0.01; }
  100% {
    opacity: 1; } }

@keyframes fadeOut {
  0% {
    opacity: 1; }
  100% {
    opacity: 0.01; } }
