.myButton {
  display: flex; }
  .myButton .button {
    position: relative;
    display: inline-block;
    width: 0.4rem;
    height: 0.4rem;
    border-radius: 50%;
    border: 1px solid #000;
    margin-bottom: 0.05333rem;
    box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.2); }
    .myButton .button::before, .myButton .button::after {
      position: absolute;
      display: block;
      content: " ";
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      border-radius: 50%;
      box-shadow: inset 0 3px 6px rgba(255, 255, 255, 0.8); }
    .myButton .button::after {
      box-shadow: inset 0 -3px 6px rgba(0, 0, 0, 0.8); }
    .myButton .button.active:before {
      box-shadow: 0 -3px 6px rgba(255, 255, 255, 0.6) inset; }
    .myButton .button.active:after {
      box-shadow: 0 5px 5px rgba(0, 0, 0, 0.6) inset; }
  .myButton .title {
    display: inline-block;
    font-size: 0.16rem;
    transform: scale(0.85); }
