/* 设计稿, 以iphone6为准 */
/* 颜色规范 */
/* 字体规范 */
* {
  box-sizing: border-box;
  user-select: none; }

html {
  font-family: "HanHei SC","PingHei","PingFang SC","STHeitiSC-Light","Helvetica Neue","Helvetica","Arial",sans-serif;
  font-size: 20vw; }
  @media screen and (max-width: 320px) {
    html {
      font-size: 64px; } }
  @media screen and (min-width: 540px) {
    html {
      font-size: 108px; } }

body {
  margin: 0 auto;
  padding: 0;
  max-width: 540px;
  min-width: 320px;
  background: #009688; }
