/* 设计稿, 以iphone6为准 */
/* 颜色规范 */
/* 字体规范 */
.keyboard {
  display: flex;
  align-items: center;
  width: 100%; }
  .keyboard .content {
    margin: 0.13333rem 0;
    position: relative;
    width: 100%;
    min-height: 2.4rem; }

.keyPause, .keySound, .keyReset {
  position: absolute;
  left: 0.4rem;
  top: 0rem; }
  .keyPause span:first-child, .keySound span:first-child, .keyReset span:first-child {
    width: 0.4rem;
    height: 0.4rem;
    background: #2dc421; }
  .keyPause span:last-child, .keySound span:last-child, .keyReset span:last-child {
    min-width: 0.4rem;
    text-align: center; }

.keySound {
  left: 1.06667rem;
  top: 0; }

.keyReset {
  left: 1.76rem;
  top: 0; }
  .keyReset span:first-child {
    width: 0.4rem;
    height: 0.4rem;
    background: #dd1a1a; }

.keyDrop {
  position: absolute;
  left: 0.64rem;
  top: 0.78667rem; }
  .keyDrop span:first-child {
    width: 1.26667rem;
    height: 1.26667rem;
    background: #5a65f1; }
  .keyDrop span:last-child {
    min-width: 1.26667rem;
    text-align: center; }

.keyRotate, .keyLeft, .keyRight, .keyDown {
  position: absolute;
  left: 3.14667rem;
  top: 0; }
  .keyRotate span:first-child, .keyLeft span:first-child, .keyRight span:first-child, .keyDown span:first-child {
    width: 0.8rem;
    height: 0.8rem;
    background: #5a65f1; }
  .keyRotate span:last-child, .keyLeft span:last-child, .keyRight span:last-child, .keyDown span:last-child {
    min-width: 0.8rem;
    text-align: center; }

.keyLeft {
  left: 2.43333rem;
  top: 0.73333rem; }

.keyRight {
  left: 3.85333rem;
  top: 0.73333rem; }

.keyDown {
  left: 3.14667rem;
  top: 1.4rem; }

.keyDecorate {
  position: absolute;
  left: 3.29333rem;
  top: 0.84rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: 0.50667rem;
  height: 0.50667rem;
  font-size: 0; }
  .keyDecorate .keyDecorateCenter {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between; }
  .keyDecorate .keyDecorateItem {
    display: block;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0.05333rem 0.05333rem 0.10667rem 0.05333rem;
    border-color: transparent transparent #111; }
